const ios = /iPad|iPhone/.test(navigator.userAgent);
const availableConcurrency = 'hardwareConcurrency' in navigator ? navigator.hardwareConcurrency : ios ? 2 : 8;
const workers = Array.from({ length: availableConcurrency }, _ => new Worker('./worker.js'));
const numberFormatter = new Intl.NumberFormat();
const numberFormat = number => numberFormatter.format(number);
const loadWords = async url => fetch(url).then(res => res.text());
const crlfRegex = /\r?\n/;

const {
    elapsedTimeElem,
    fiveLetterWordCountElem,
    normalizedWordCountElem,
    solutionCountElem,
    solutionsElem,
    solveFormElem,
    wordCountElem,
    workersWorkingElem,
    workItemsElem,
} = window;

const {
    customWordFile: customWordFileElem,
    solveButton: solveButtonElem,
    concurrency: concurrencyElem,
    wordList: wordListElems,
} = solveFormElem;

const customWordListElem = [...wordListElems].find(({ value }) => value === 'custom');
const wordlListElem = [...wordListElems].find(({ value }) => value.match(/wordl/));
const wordsAlphaListElem = [...wordListElems].find(({ value }) => value.match(/words_alpha/));

for (let n = 0; n < availableConcurrency; ++n) {
    const option = document.createElement('option');
    option.append(n + 1);
    concurrencyElem.append(option);
}

concurrencyElem.value = availableConcurrency;
customWordFileElem.addEventListener('change', e => (wordListElems.value = 'custom'));
customWordListElem.addEventListener('change', e => {
    if (wordListElems.value === 'custom' && !customWordFileElem.value) {
        customWordFileElem.click();
    }
});

const solve = wordsString => {
    const startTime = Date.now();

    solveButtonElem.innerText = 'Solving...';
    solutionsElem.innerHTML = '';
    solutionCountElem.innerText = 0;

    const allWords = wordsString.split(crlfRegex).map(word => word.trim().toLowerCase());
    wordCountElem.innerText = numberFormat(allWords.length);

    const fiveLetterWords = allWords.filter(word => /^[a-z]{5}$/.test(word));
    fiveLetterWordCountElem.innerText = numberFormat(fiveLetterWords.length);

    // normalizedWords are fiveLetterWords with their letters rearranged so that they are sorted (WORDS becomes DORSW).
    // Anagrams are removed from consideration by searching for normalizedWords instead of actual words since all anagrams
    // map to the same normalizedWord.  Normalized words with any repeating characters /(.)\1/ are also removed.
    const normalizedWordMap = new Map();
    for (const word of fiveLetterWords) {
        const normalizedWord = [...word].sort().join('');
        // ignore normalized words with repeating characters
        if (!normalizedWord.match(/(.)\1/)) {
            const entry = normalizedWordMap.get(normalizedWord);
            // combine anagrams for display later
            normalizedWordMap.set(normalizedWord, entry ? `${entry}/${word}` : word);
        }
    }
    normalizedWordCountElem.innerText = numberFormat(normalizedWordMap.size);

    const a = 'a'.charCodeAt(0);

    const words = [...normalizedWordMap.keys()];
    const options = Object.fromEntries(words.map(word => [word, word.split('')]));

    const allChars = new Set(Object.values(options).flat());
    if (allChars.size !== 26) {
        const missingChars = Array.from({ length: 26 }, (_, i) => String.fromCharCode(a + i)).filter(char => !allChars.includes(char));
        solutionsElem.appendChild(document.createElement('li')).append(`Invalid word list. Missing letters: ${missingChars.join(', ')}`);
        return;
    }

    const solutions = new Set();
    const displaySolutions = [];

    const workItems = Array.from({ length: 26 }, (_, i) => String.fromCharCode(a + i))
        .map(char => Object.fromEntries(Object.entries(options).filter(([word]) => !word.includes(char))))
        // sort work in descending order of options to reduce the time long-pole dangling workers require
        .sort((workItem1, workItem2) => Object.keys(workItem2).length - Object.keys(workItem1).length);

    const recordSolution = solution => {
        // ignore solutions that contain the same normalized words as an already recorded solution, but in a different order
        const normalizedSolution = solution.sort().join(',');
        if (!solutions.has(normalizedSolution)) {
            solutions.add(normalizedSolution);
            solutionCountElem.innerText = numberFormat(solutions.size);
            displaySolutions.push(
                normalizedSolution
                    .split(',')
                    .map(step => normalizedWordMap.get(step))
                    .sort()
                    .join(', ')
            );
        }
    };

    const render = _ => {
        // append list elements for newly arrived solutions since last render
        for (let i = solutionsElem.children.length; i < displaySolutions.length; ++i) {
            solutionsElem.appendChild(document.createElement('li')).append(displaySolutions[i]);
        }
        elapsedTimeElem.innerText = `${numberFormat((Date.now() - startTime) / 1000)} seconds`;
        solutionsElem.scrollTop = solutionsElem.scrollHeight;
    };

    const renderInterval = setInterval(_ => requestAnimationFrame(render), 60);

    let currentWorkItem = 0;
    let completedWorkItems = 0;

    const concurrency = concurrencyElem.value;

    return new Promise((resolve, reject) => {
        for (const [n, worker] of workers.entries()) {
            if (n >= concurrency) {
                break;
            }
            workersWorkingElem.value = n + 1;
            const messageHandler = msg => {
                switch (msg.data.type) {
                    case 'solution': {
                        recordSolution(msg.data.solution);
                        break;
                    }

                    case 'completed': {
                        if (++completedWorkItems < workItems.length) {
                            if (currentWorkItem < workItems.length) {
                                worker.postMessage({ type: 'solve', options: workItems[currentWorkItem++] });
                            } else {
                                workersWorkingElem.value -= 1;
                                worker.removeEventListener('message', messageHandler);
                            }
                        } else if (completedWorkItems === workItems.length) {
                            const elapsedSeconds = (Date.now() - startTime) / 1000;
                            worker.removeEventListener('message', messageHandler);
                            workersWorkingElem.value -= 1;
                            elapsedTimeElem.innerText = `${numberFormat(elapsedSeconds)} seconds`;
                            solveButtonElem.disabled = false;
                            solveButtonElem.innerHTML = 'Solve';
                            clearInterval(renderInterval);
                            resolve();
                        }
                        workItemsElem.value = workItems.length - completedWorkItems;
                        break;
                    }
                }
            };

            if (currentWorkItem < workItems.length) {
                worker.addEventListener('message', messageHandler);
                worker.postMessage({ type: 'solve', options: workItems[currentWorkItem++] });
            }
        }
    });
};

solveButtonElem.addEventListener('click', async e => {
    e.preventDefault();
    solveButtonElem.disabled = true;
    solveButtonElem.innerText = 'Loading...';
    const formData = Object.fromEntries([...new FormData(solveFormElem)]);
    if (formData.wordList === 'custom') {
        var reader = new FileReader();
        reader.onload = e => solve(event.target.result);
        reader.readAsText(formData.customWordFile);
    } else if (formData.wordList === 'intersection') {
        loadWords(wordlListElem.value).then(str1 => {
            const list1 = str1.split(crlfRegex);
            loadWords(wordsAlphaListElem.value).then(str2 => {
                const list2 = new Set(str2.split(crlfRegex));
                solve(list1.filter(word => list2.has(word)).join('\n'));
            });
        });
    } else {
        loadWords(formData.wordList).then(solve);
    }
});
